.material-icons {
  font-family: 'Material Icons' !important;           /* Carga la fuente de Google */
  font-weight: normal;
  font-style: normal;
  font-variant-ligatures: contextual !important;      /* Activa las ligaduras para iconos */
  text-rendering: optimizeLegibility !important;
  /* Ajustes opcionales de tamaño y alineación */
  font-size: 1.2em;
  line-height: 1;
  vertical-align: middle;
}

.custom-name-cell {
  display: inline-flex;
  align-items: center;
  gap: 0.5rem;        /* espacio icono ↔ texto */
}

.custom-name-cell .file-name {
  /* opcional: controlar el wrap o ellipsis */
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

/* Dataset (tabla) para tema Material */
.e-filemanager .e-icons.dataset-icon::before {
  content: "\\f8ee";               /* U+E1DB → storage */
  font-family: 'Material Icons';
  font-variant-ligatures: contextual;
  font-size: large !important;
  color: #FF9900;                 /* naranja AWS */
}

/* Bucket (almacenamiento) para tema Material */
.e-filemanager .e-icons.bucket-icon::before {
  content: "\\e1db";               /* U+E1DB → storage */
  font-family: 'Material Icons';
  font-variant-ligatures: contextual;
  font-size: large !important;
  color: #2ca02c;                 /* naranja AWS */
}

